table.statistics-confusion {
  position: relative;
  margin: auto;
  table-layout: fixed;
  border-collapse: collapse;
  color: #000;
}
table.statistics-confusion a {
  color: #000;
  text-decoration-style: dotted;
}
table.statistics-confusion a:hover {
  color: #008;
  text-decoration: underline;
}
table.statistics-confusion a:hover:visited {
  color: #808;
}
table.statistics-confusion .term {
  display: inline-block;
}
table.statistics-confusion .term.highlight {
  background: rgba(238, 255, 136, 0.5);
  box-shadow: 0 0 0.2em 0.2em rgba(238, 255, 136, 0.5);
}
table.statistics-confusion .term[data-term='TP']::first-letter,
table.statistics-confusion .term[data-term='FN']::first-letter {
  color: #880000;
}
table.statistics-confusion .term[data-term='FP']::first-letter,
table.statistics-confusion .term[data-term='TN']::first-letter {
  color: #008800;
}
table.statistics-confusion .term[data-term='TP'],
table.statistics-confusion .term[data-term='FP'] {
  color: #cc4400;
}
table.statistics-confusion .term[data-term='FN'],
table.statistics-confusion .term[data-term='TN'] {
  color: #0044cc;
}
table.statistics-confusion .sfrac {
  display: inline-block;
  vertical-align: -0.6em;
  white-space: nowrap;
}
table.statistics-confusion .sfrac .num,
table.statistics-confusion .sfrac .den {
  display: block;
  text-align: center;
}
table.statistics-confusion .sfrac .den {
  border-top: solid 1px;
}
table.statistics-confusion .sfrac .aux {
  position: absolute;
  left: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
table.statistics-confusion caption {
  height: 4rem;
  line-height: 4rem;
  font-size: 150%;
}
table.statistics-confusion td,
table.statistics-confusion th {
  width: 12rem;
  min-width: 12rem;
  height: 7rem;
  min-height: 7rem;
  text-align: center;
  border: solid 1px #888;
  padding: 0;
}
table.statistics-confusion td.highlight,
table.statistics-confusion th.highlight {
  background: rgba(238, 255, 136, 0.4);
}
table.statistics-confusion td .formula,
table.statistics-confusion th .formula {
  font-weight: normal;
  margin-top: .5em;
}
table.statistics-confusion td .formula:first-child,
table.statistics-confusion th .formula:first-child {
  margin: 0;
}
table.statistics-confusion button {
  position: absolute;
  width: 2rem;
  height: 2rem;
  font-size: 100%;
  line-height: 100%;
  padding: 0;
  border: solid 1px #888;
  color: #000;
  background: rgba(255, 255, 255, 0.75);
}
table.statistics-confusion button:hover,
table.statistics-confusion button:focus {
  background: #DDF;
}
table.statistics-confusion button.swap-rc {
  top: 3rem;
  left: -1rem;
}
table.statistics-confusion button.swap-c {
  top: 3rem;
  left: calc(23rem + 2px);
}
table.statistics-confusion button.swap-r {
  top: calc(17rem + 2px);
  left: -1rem;
}
table.statistics-confusion button.add-c {
  top: 3rem;
  left: calc(47rem + 4px);
}
table.statistics-confusion button.add-r {
  top: calc(31rem + 4px);
  left: -1rem;
}
table.statistics-confusion button svg {
  display: block;
}
@media print {
  table.statistics-confusion a {
    text-decoration: none;
  }
  table.statistics-confusion button {
    display: none;
  }
}
